<template>
  <div class=page>
    <header>
      <Topbar/>
    </header>
    <main>
      <ResumeEditor/>
      <ResumePreview/>
    </main>
  </div>
</template>

<script>
  import 'normalize.css/normalize.css'
  import './assets/reset.css'
  import Topbar from './components/Topbar'
  import ResumeEditor from './components/ResumeEditor'
  import ResumePreview from './components/ResumePreview'

  export default {
    name: 'app',
    data: function () {
      return {
        text: '你好'
      }
    },
    components: {
      Topbar,
      ResumeEditor,
      ResumePreview
    }
  }
</script>

<style lang="less" type="text/less">
  .page {
    height: 100vh;
    display: flex;
    flex-direction: column;
    background: #EAEBEC;
    & > main {
      flex-grow: 1;
    }
    & > main {
      min-width: 1024px;
      max-width: 1440px;
      display: flex;
      margin-top:16px;
      margin-bottom: 16px;
      justify-content: space-between;
      width:100%; // 不加的话main只有1024px
      align-self: center;
    }
  }

  #resumeEditor {
    width: 35%;
    background: #444;
  }

  #resumePreview {
    flex-grow: 1;
    margin-left: 16px;
    background: #777;
  }

</style>



